import { Brightness5Outlined, DarkModeOutlined } from "@mui/icons-material";
import { Typography, ButtonGroup, IconButton, AppBar, Box, Toolbar } from "@mui/material";
import { useState, useEffect } from "react";
import "./myHeader.css";

function MyHeader({ setDark, setBright }: any): JSX.Element {
    const [time, setTime] = useState("");

    useEffect(() => {
        setInterval(() => {
            setTime(new Date().toString().split("G")[0]);
        }, 1000);
    }, []);

    return (
        <div className="myHeader">
            <Box sx={{ flexGrow: 1 }}>
                <AppBar position="static">
                    <Toolbar>
                        <Typography variant="h6" component="div">
                            {time}
                        </Typography>
                        <Typography variant="h5" component="div" sx={{ flexGrow: 1 }}>
                            Currency Converter
                        </Typography>
                        <ButtonGroup variant="outlined">
                            <IconButton onClick={setBright}>
                                <Brightness5Outlined />
                            </IconButton>
                            <IconButton onClick={setDark}>
                                <DarkModeOutlined />
                            </IconButton>
                        </ButtonGroup>
                    </Toolbar>
                </AppBar >
            </Box >
        </div >
    );
}

export default MyHeader;
