import { useEffect, useState } from "react";
import "./mainPage.css";
import { Button, FormControl, FormHelperText, IconButton, InputLabel, MenuItem, Select, SelectChangeEvent, TextField, Typography } from '@mui/material';
import { useForm } from "react-hook-form";
import Currency from './../../../modal/currency';
import currencies from './../../../utils/currencyMap';
import axios from "axios";
import globals from "../../../utils/global";
import notify, { ErrorMessage, SuccessMessage } from './../../../utils/notify';
import { SwapVert } from "@mui/icons-material";

function MainPage(): JSX.Element {
    const { handleSubmit, formState: { errors } } = useForm<Currency>();
    const [date, setDate] = useState("");
    const [currency, setCurrency] = useState<Currency>(new Currency("Automatic", "Automatic", 1));

    useEffect(() => {
        setDate(new Date().toLocaleDateString());
    }, []);

    const handleChange = (args: any) => {
        const { name, value } = args.target;
        setCurrency({
            ...currency,
            [name]: value
        });
    };

    const handleSelectChange = (event: SelectChangeEvent) => {
        const { name, value } = event.target;
        setCurrency({
            ...currency,
            [name]: value
        });
    };

    const switchCurrencies = () => {
        const from = currency.from;
        const to = currency.to;

        setCurrency({
            ...currency,
            ["from"]: to,
            ["to"]: from,
        })
    };

    const send = () => {
        if (currency.from === "Automatic" || currency.to === "Automatic") {
            notify.error(ErrorMessage.SELECT_CURRENCIES);
            return;
        }

        if (currency.amount <= 0) {
            notify.error(ErrorMessage.ENTER_AMOUNT);
            return;
        }

        axios.get(`${globals.URLs.exchangeURL}/${currency.from}/${currency.to}/${currency.amount}`)
            .then((response) => {
                setCurrency({
                    ...currency,
                    ["sum"]: response.data.sum,
                });
                notify.success(SuccessMessage.SUCCESS);
            })
            .catch((err) => {
                setCurrency({
                    ...currency,
                    ["sum"]: 0,
                });
                console.log(err);
                notify.error(ErrorMessage.MALFUNCTION);
            })
    };


    return (
        <div className="mainPage">
            <Typography variant="h4" component={"h4"} color={"inherit"} fontFamily={"fantasy"}>
                All Currencies are updated as of {date}
            </Typography>
            <hr />

            <form className="solidBox" onSubmit={handleSubmit(send)}>
                <h1>Convert</h1> <hr />
                <TextField type={"number"} name="amount" label="Amount" variant="outlined" value={currency.amount} onChange={handleChange} />

                <FormHelperText sx={{ textAlign: "center" }}>
                    {errors.amount?.message}
                </FormHelperText> <br />

                <FormControl sx={{ width: "400px" }}>
                    <InputLabel id="from-select-label">From</InputLabel>
                    <Select
                        labelId="from-select-label"
                        id="from-select"
                        name="from"
                        label="From"
                        value={currency.from}
                        onChange={handleSelectChange}>
                        {[...currencies.entries()].map((entry, index) => <MenuItem key={index} value={entry[0]}>{entry[1]}</MenuItem>)}
                    </Select>

                    <FormHelperText sx={{ textAlign: "center" }}>
                        {errors.from?.message}
                    </FormHelperText>
                </FormControl> &nbsp;

                <TextField type={"text"} label="Initials (From)" variant="outlined" disabled value={currency.from} /> <br />

                <IconButton onClick={switchCurrencies}>
                    <SwapVert />
                </IconButton> <br />

                <FormControl sx={{ width: "400px" }}>
                    <InputLabel id="to-select-label">To</InputLabel>
                    <Select
                        labelId="to-select-label"
                        id="to-select"
                        name="to"
                        label="To"
                        value={currency.to}
                        onChange={handleSelectChange}>
                        {[...currencies.entries()].map((entry, index) => <MenuItem key={index} value={entry[0]}>{entry[1]}</MenuItem>)}
                    </Select>

                    <FormHelperText sx={{ textAlign: "center" }}>
                        {errors.to?.message}
                    </FormHelperText> <br />
                </FormControl> &nbsp; &nbsp;

                <TextField type={"text"} label="Initials (To)" variant="outlined" disabled value={currency.to} /> <br />

                <Button type="submit" color="primary" variant="contained">Convert Currency</Button>
            </form> <br /> <br />

            <TextField type={"text"} name="sum" label="Sum" variant="outlined" value={currency.sum} disabled sx={{ backgroundColor: "beige" }} />
        </div>
    );
}

export default MainPage;
