import { Notyf } from "notyf";

export enum SuccessMessage {
    SUCCESS = "Converted Successfully",
}

export enum ErrorMessage {
    SELECT_CURRENCIES = "Please select currencies",
    ENTER_AMOUNT = "Please enter an amount larger than 0",
    MALFUNCTION = "Something went wrong, please try again",
}

class Notify {
    private notification = new Notyf({ duration: 4000, position: { x: "center", y: "top" } });

    public success(message: string) {
        this.notification.success(message);
    }

    public error(message: string) {
        const errorMessage = this.extractMessage(message);
        this.notification.error(errorMessage);
    }

    private extractMessage(err: any): string {
        if (typeof err === "string") {
            return err;
        }
        if (typeof err?.response?.data === "string") {
            return err.response.data;
        }
        if (Array.isArray(err?.response?.data)) {
            return err.response.data[0];
        }
        if (typeof err?.message === "string") {
            return err.message;
        }
        return ErrorMessage.MALFUNCTION;
    }

}

const notify = new Notify();

export default notify;